function pageReady(callback) {
    if (document.readyState !== "loading") {
        callback();
    } else document.addEventListener("DOMContentLoaded", callback);
}


let buttonsNodeList = document.querySelectorAll(".m-btn");
buttonsNodeList.forEach((btn) => {

    btn.addEventListener("mouseenter", function (e) {
        let target = e.target;
        let x = e.clientX - target.offsetLeft;
        let y = e.clientY - target.offsetTop;
        let rEffect = document.createElement("span");
        rEffect.classList.add("_anim-el")

        rEffect.style.left = `${x}px`;
        rEffect.style.top = `${y}px`;

        this.appendChild(rEffect);
        setTimeout(() => {
            rEffect.remove();
        }, 850);
    });
});



const Button = {
    init: () => {
        Button.rippleEffectMovement();
    },

    rippleEffectMovement: () => {

    }
};

pageReady(() => {
    Button.init();
});


const animatedClassName = "animated";
const ELEMENTS = document.querySelectorAll(".HOVER");
const ELEMENTS_SPAN = [];

ELEMENTS.forEach((element, index) => {
    let addAnimation = false;
    // On the last element in the page, add a listener to remove
    // ---> animation-class when animation ends
    if (element.classList[1] == "FLASH") {
        g = element;
        element.addEventListener("animationend", e => {
            element.classList.remove(animatedClassName);
        });
        addAnimation = true;
    }

    // If The span element for this element does not exist in the array, add it.
    if (!ELEMENTS_SPAN[index])
        ELEMENTS_SPAN[index] = element.querySelector("._line");

    element.addEventListener("mouseover", e => {
        ELEMENTS_SPAN[index].style.left = e.pageX - element.offsetLeft + "px";
        ELEMENTS_SPAN[index].style.top = e.pageY - element.offsetTop + "px";

        // Add an animation-class to animate via CSS.
        if (addAnimation) element.classList.add(animatedClassName);
    });

    element.addEventListener("mouseout", e => {
        ELEMENTS_SPAN[index].style.left = e.pageX - element.offsetLeft + "px";
        ELEMENTS_SPAN[index].style.top = e.pageY - element.offsetTop + "px";
    });
});
