let headerSearch = document.querySelector(".header-search")
let headerSearchField = document.querySelector(".header-search .search-field input")

if(headerSearchField){
    headerSearchField.oninput = function () {
        if(this.value.length > 0){
            headerSearch.classList.add("_active")
        }
    }
    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(headerSearchField);

        if ( ! withinBoundaries ) {
            if(headerSearch.classList.contains("_active")){
                headerSearch.classList.remove("_active")
            }
        }
    })
}

$(".profile-dropdown__item").click(function () {
    let tabId = $(this).attr("data-tab")
    $(`.lk ._tab[data-tab="${tabId}"] `).click()
})

$("._show-profile-dropdown").click(function () {
    $(".header-profile").toggleClass("_active")
})
document.addEventListener( 'click', (e) => {
    const div = document.querySelector( '.header-profile');
    const withinBoundaries = e.composedPath().includes(div);

    if ( ! withinBoundaries ) {
        if($(".header-profile").hasClass("_active")){
            $(".header-profile").removeClass("_active")
        }
    }
})

