$.mask.definitions['9'] = '';
$.mask.definitions['d'] = '[0-9]';

$('._card-field').mask('dddd dddd dddd dddd', { autoclear: false }, { placeholder: '1234 5678 9876 0000' });

window.addEventListener('DOMContentLoaded', () => {
    $(".fg select").niceSelect()

    fix100vh();
    setPlaceholders()
    getScrollBarSize()
    window.addEventListener('resize', () => {
        fix100vh();
        setPlaceholders()
        getScrollBarSize()
    })




})

function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

function setPlaceholders() {
    let placeholderFields = document.querySelectorAll("[data-placeholder]")

    placeholderFields.forEach(field => {
        let placeholderDesktop = field.getAttribute("data-placeholder")
        let placeholderMob = field.getAttribute("data-placeholder2")

        if(window.innerWidth>576){
            field.setAttribute("placeholder",placeholderDesktop)
        } else{
            field.setAttribute("placeholder",placeholderMob)
        }
    })
}

function setLabelPlace(){
    let fields = document.querySelectorAll(".fg._type2 input,.fg._type2 select,.fg._type2 textarea")

    fields.forEach(field => {
        setLabelPosition(field)

        field.onchange = ()=> {
            setLabelPosition(field)
        }

        function setLabelPosition() {
            if(field.value !== ""){
                field.classList.add("_filled")
            } else{
                field.classList.remove("_filled")
            }
        }
    })
}


setLabelPlace()


$("._tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    console.log(parentBlock)
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})

$(".qa-card__show").click(function () {
    $(this).parents(".qa-card").toggleClass("_active")
    $(this).siblings(".qa-card__hidden").slideToggle()
})
