$(".catalog-nav__show").click(function () {
    $(this).parents(".catalog-nav__item").toggleClass("_open")
    $(this).siblings(".catalog-nav__hidden").slideToggle()
})

$(".filter__toggle").click(function () {
    $(".filter").toggleClass("_open")
})

$(document).mouseup( function(e){
    var div = $( ".filter" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
        }
    }
});

$("._show-more-terms").click(function () {
    $(".term").toggleClass("_show-all")
})
